import { StorageUtil } from './../storage';
import { Injectable } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';

@Injectable({
  providedIn: 'root'
})
export class LoginCookieStorage {

  constructor(private cookieService: CookieService) { }

  private login = 'L7R6T';

  public set( value ): void{
    this.cookieService.set(this.login, JSON.stringify( value ));
  }

  public get( ): any{

    const login = this.cookieService.get(this.login);

    if(login){
      return JSON.parse(this.cookieService.get(this.login));
    }

    return null;
   }

   public remove():void{
     this.cookieService.deleteAll();
   }
}
