import { MenuModel } from './app.menu.model';
import {Component, OnInit} from '@angular/core';
import {AppMainComponent} from '../../app.main.component';

@Component({
    selector: 'app-menu',
    templateUrl: './app.menu.component.html',
})
export class AppMenuComponent implements OnInit {

    model: any[];
    listItemsMenuPerfil = [];

    constructor(public menuModel: MenuModel, public app: AppMainComponent) {}

    ngOnInit() {
        this.model = this.menuModel.model;
    }

    onMenuClick(event) {
        this.app.onMenuClick(event);
    }
}
