import { ElementsResponse } from './../../shared/model/response/elementsResponse';
import { environment } from './../../../environments/environment';
import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor( private httpClient: HttpClient ) { }

  public get<T>( path: string, params): Observable<T> {

   return this.httpClient.get<T>(`${environment.url}/${path}`, {
      params: params,
      headers: {},
    });
  }

  public getById<T>( path: string, params?: HttpParams ): Observable<T>{

    return this.httpClient.get<T>(`${environment.url}/${path}`, {
       params: params,
       headers: {},
     });
   }

  public post<T>( path: string, payload: any, params?: HttpParams ): Observable<T>{

    return this.httpClient.post<T>(`${environment.url}/${path}`, payload,{
       params: params,
       headers: {},
     });
   }

   public update<T>( path: string, payload , params?: HttpParams): Observable<T>{

    return this.httpClient.patch<T>(`${environment.url}/${path}`, payload,{
       params: params,
       headers: {},
     });
   }

   public updateAll<T>( path: string, payload , params?: HttpParams): Observable<T>{

    return this.httpClient.put<T>(`${environment.url}/${path}`, payload,{
       params: params,
       headers: {},
     });
   }

   public delete<T>( path: string, params?: HttpParams ): Observable<T>{

    return this.httpClient.delete<T>(`${environment.url}/${path}`,{
       params: params,
       headers: {},
     });
   }

   public search( path: string, params?: HttpParams ): Observable<any> {

    return this.httpClient.get<ElementsResponse>(`${environment.url}/${path}`, {
       params: params,
       headers: {},
     }).pipe(map(r => {
        return r;
     }));
   }
}
