import { Component } from '@angular/core';
import { interval, Subscription } from 'rxjs';
import { LoginCookieStorage } from './core/cookieStorage/loginCookieStorage';
import { ApiService } from './core/services/api.service';
import { StorageUtil } from './core/storage';
import { ApiPaths } from './shared/enuns/api-paths.enum';
import { StartPerfil } from './startPeril';

declare var google;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  
  interval = new Subscription();
  private user;

  constructor(
    private service: ApiService,
    public startPerfil: StartPerfil,
    private loginCookieStorage: LoginCookieStorage) {
    
    this.user = this.loginCookieStorage.get();
    this.loadPerfil();
    this.loadTrackback();
  }

  private loadPerfil(): void {
    if (StorageUtil.loadPerfil()) {
      this.service.get(`${ApiPaths.PERFIL}/${this.loginCookieStorage.get().perfil}`, {}).subscribe((r: any) => {
        this.startPerfil.listItemsMenuPerfil = r[0].menuItens;
      });
    }
  }

  private loadTrackback(){
    if(this.user?.trackback){
      setTimeout(() => {
        this.interval = interval(8000).subscribe(() => {
          this.myPosition();
       });
      }, 3000);

      }
  }

  private async myPosition(): Promise<void> {

    const { name, id } = this.loginCookieStorage.get();
    let location = { lat: null, lng: null };

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(function (position) {

        location.lat = position.coords.latitude;
        location.lng = position.coords.longitude;
      },

        function error(msg) { alert('Please enable your GPS position feature.'); },

        {
          maximumAge: 10000,
          timeout: 5000,
          enableHighAccuracy: true
        });

    } else {
      alert("Geolocation API is not supported in your browser.");
    }

    setTimeout(() => {
      this.saveLocation(location)
    }, 1000);

  }

  private saveLocation(payload): void {

    const { id } = this.loginCookieStorage.get();

    this.service.update(`${ApiPaths.GMAP}/${id}`, payload).subscribe();
  }
}
