import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class StartPerfil {

  public listItemsMenuPerfil = [];
  constructor(){}

  public findAuthorizationPerfil(value): any{

    const i = this.listItemsMenuPerfil.find((key) => key == value.key);

        if(i){
          return true;
        }

        return false;
   }

}
