import { LoginCookieStorage } from './../../core/cookieStorage/loginCookieStorage';
import { Component, OnDestroy } from '@angular/core';
import { AppMainComponent } from '../../app.main.component';
import { BreadcrumbService } from '../config/app.breadcrumb.service';
import { Subscription } from 'rxjs';
import { MenuItem } from 'primeng/api';
import { StorageUtil } from 'src/app/core/storage';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/core/services/api.service';
import { ApiPaths } from '../enuns/api-paths.enum';

@Component({
  selector: 'app-topbar',
  templateUrl: './app.topbar.component.html',
})
export class AppTopBarComponent implements OnDestroy {
  subscription: Subscription;
  user: any;
  items: MenuItem[];
  size: number;
  alerts = [];
  load = true;

  constructor(
    public breadcrumbService: BreadcrumbService,
    private service: ApiService,
    private loginCookieStorage: LoginCookieStorage,
    private router: Router,
    public app: AppMainComponent
  ) {
    this.user = this.loginCookieStorage.get();
    this.subscription = breadcrumbService.itemsHandler.subscribe((response) => {
      this.items = response;
    });

    this.loadAlertSize();
  }

  private loadAlertSize(): void {
    const params = {
      id: this.loginCookieStorage.get().id,
      notify: this.loginCookieStorage.get().perfil,
    };

    const subscription = this.service
      .get(`${ApiPaths.ALERT_SIZE}`, params)
      .subscribe((element: number) => (this.size = element));

    this.subscription.add(subscription);
  }

  public loadAlerts(): void {
    this.load = true;
    this.alerts = [];
    const params = {
      id: this.loginCookieStorage.get().id,
      notify: this.loginCookieStorage.get().perfil,
    };

    const subscription = this.service
      .get(`${ApiPaths.ALERT_USERS}`, params)
      .subscribe((r: any) => {
        this.alerts = r.elements;
        this.load = false;
      });

    this.subscription.add(subscription);
  }

  public logout(): void {
    this.loginCookieStorage.remove();
    StorageUtil.clear();
    this.router.navigate(['/login']);
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
