import { environment } from './../../environments/environment.prod';

export class StorageUtil {
  private static USER = 'USER';
  private static LAYOUT = 'LAYOUT';
  private static COLOR_SCHEME = 'C_SCHEME';
  private static LOAD_PERFIL = 'LPF';

  /* decript */

  private static encryptSecretKey = ''; //environment.passWord

  /* outros */


  public static layout(data: any): void {
    localStorage.setItem(StorageUtil.LAYOUT, data);
  }

  public static loadlayout(): any {
    return localStorage.getItem(StorageUtil.LAYOUT);
  }

  public static colorScheme(data: any): void {
    localStorage.setItem(StorageUtil.COLOR_SCHEME, data);
  }

  /* GET */

  public static loadcolorScheme(): any {
    return localStorage.getItem(StorageUtil.COLOR_SCHEME);
  }

  public static setLoadPerfil(): any {
    return sessionStorage.setItem(StorageUtil.LOAD_PERFIL, 'true');
  }

  public static loadPerfil(): any {
    return sessionStorage.getItem(StorageUtil.LOAD_PERFIL);
  }

  public static getUser(): any {
    return JSON.parse(localStorage.getItem(StorageUtil.USER));
  }

  public static setUserPerfil(data: any): void {
    localStorage.setItem('perfil', data);
  }

  public static getUserPerfil(): any {
    return localStorage.getItem('perfil');
  }

  public static clear(): void {
    sessionStorage.clear();
  }

}
