import { LoginCookieStorage } from './../../core/cookieStorage/loginCookieStorage';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { StorageUtil } from './../../core/storage';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { INPUT_VALIDATE_ERROR } from 'src/app/core/menssages/menssagens';
import { MsgService } from 'src/app/core/menssages/msg.service';
import { ApiService } from 'src/app/core/services/api.service';
import { ApiPaths } from 'src/app/shared/enuns/api-paths.enum';
import { TOAST_ERROR, TOAST_SUCCESSO } from 'src/app/core/menssages/toastEvent';
import { ValidateDoc } from 'src/app/shared/utils/validateDoc';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  public loginFormGroup: FormGroup;
  public registerFormGroup: FormGroup;
  public unSubscription = new Subscription();
  public crtUser: boolean;
  public rPasswaord: string;
  public img = {base64: null, img: 'http://admin.transvinha.com:82/img//avatar.jpg'};

  constructor(
    private _formBuilder: FormBuilder,
    private service: ApiService,
    private spinner: NgxSpinnerService,
    private loginCookieStorage: LoginCookieStorage,
    private msg: MsgService,
    public router: Router
  ) {}

  private loadFromGroup(): void {
    this.loginFormGroup = this._formBuilder.group({
      login: [null, [Validators.required]],
      password: [null, [Validators.required]],
    });

    this.registerFormGroup = this._formBuilder.group({
      login: ['', [Validators.required]],
      password: ['', [Validators.required]],
     /*  accTerms: ['', [Validators.required]], */
      imgUrl : ['']
     },
    );
  }

  public updateImage(): void{
    const element: HTMLElement = document.getElementById('inputFile');
    element.click();
  }

  public setImage(event): void {
    var reader = new FileReader();
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsBinaryString(event.target.files[0]);
  }

  _handleReaderLoaded(readerEvt) {
    var binaryString = readerEvt.target.result;
    this.img.img = 'data:image/jpeg;base64,' + btoa(binaryString);
    this.img.base64 = btoa(binaryString);
  }

  public login() {

    if (this.loginFormGroup.invalid) {
       this.msg.toast(TOAST_ERROR(INPUT_VALIDATE_ERROR));
      return;
    }

    console.log(ValidateDoc.validateCPF(this.loginFormGroup.get('login')),
  this.loginFormGroup.get('login'))

    if(ValidateDoc.validateCPF(this.loginFormGroup.get('login')) == null){
      this.loginFormGroup.value.login = this.loginFormGroup.value.login.replace(/\D+/g, '');
    }

    this.spinner.show();
    this.unSubscription = this.service
                              .post(ApiPaths.LOGIN, this.loginFormGroup.value)
                              .subscribe((r) => {
                                    this.spinner.hide();
                                    this.loginCookieStorage.set(r);
                                    this.router.navigate(['app-load']);
      });
  }

  private validatePayload(){
    if(this.registerFormGroup.value.password !== this.rPasswaord){
      throw new EvalError( this.msg.toast(TOAST_ERROR('Senhas devem ser iguais')));
    }

    if (!this.img.base64) {
      throw new EvalError( this.msg.toast(TOAST_ERROR("Escolha uma boa foto para continuar!")));
    }
  }

  public register(): void{
    this.validatePayload();

    this.registerFormGroup.value.imgUrl = this.img.base64;
    this.spinner.show();
    this.unSubscription = this.service
                              .updateAll(ApiPaths.USER, this.registerFormGroup.value)
                              .subscribe((r) => {
                                    this.spinner.hide();
                                    this.crtUser = false;
                                    this.msg.toast(TOAST_SUCCESSO('Usurio cadastrado com sucesso'))
      });
  }

  ngOnInit() {
    this.loadFromGroup();
  }

  ngOnDestroy() {
    this.unSubscription.unsubscribe();
  }
}
