import { StorageUtil } from './../core/storage';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoginCookieStorage } from '../core/cookieStorage/loginCookieStorage';
import { ApiService } from '../core/services/api.service';
import { ApiPaths } from '../shared/enuns/api-paths.enum';
import { StartPerfil } from '../startPeril';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-appload',
  templateUrl: './appload.component.html',
  styleUrls: ['./appload.component.scss']
})
export class ApploadComponent implements OnInit {

  subscription = new Subscription();

  constructor(
                private service: ApiService,
                private loginCookieStorage: LoginCookieStorage,
                public startPerfil: StartPerfil,
                private router: Router,
             ) { }

  private loadPerfil(): void{
  const subscription = this.service
                           .get(`${ApiPaths.PERFIL}/${this.loginCookieStorage.get().perfil}`, {})
                           .subscribe((r: any) => {

            this.startPerfil.listItemsMenuPerfil = r[0].menuItens;
            StorageUtil.setLoadPerfil();
            this.router.navigate(['']);

    });

    this.subscription.add(subscription);
  }

  ngOnInit(): void {
   this.loadPerfil();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}
