import { CUSTUMER_ICON, ME, SUPLIER_ICON, VEHICLE_ICON } from './icons-map';
import { LoginCookieStorage } from './../core/cookieStorage/loginCookieStorage';
import { ApiPaths } from './../shared/enuns/api-paths.enum';
import { ApiService } from './../core/services/api.service';
import { map } from 'rxjs/operators';
import { Component, EventEmitter, OnInit } from '@angular/core';
import { MessageService } from 'primeng/api';
import { DeviceType } from '../shared/utils/device-type';
import { interval, Subscribable, Subscription } from 'rxjs';
import { StartPerfil } from '../startPeril';

declare var google: any;

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss'],
})
export class MapComponent implements OnInit {
  
  public options: any;
  public overlays: any[] = [];
  public data: any = '';
  public dialogVisible: boolean;
  public markerTitle: string;
  public infoWindow = new Array<any>();
  private location = {lat: null, lng: null};
  public isShowMarkerLabel: boolean;
  private marker$ = new EventEmitter<any>();
  public map: any;
  public style;
  private mobile = DeviceType.mobileType();
  public showInf: boolean = false;
  public mks: boolean = false;

  interval = new Subscription();
  public componentKey = {key: "mapa_mostrar_marcadores"};

  constructor( private messageService: MessageService, 
               private apiService: ApiService,
               public startPerfil: StartPerfil,
               private loginCookieStorage:LoginCookieStorage
             ) {}

  ngOnInit() {
    this.loadStyle();
    this.startLocalization();
  }

  private startLocalization(){
     this.regionMyPosition();

     setTimeout(() => {
      this.trakinback();
     }, 2000);
  
  }

  private getLocationMarkers(){
   console.log(  this.overlays)
    this.apiService.get<any>(ApiPaths.GMAP_MARKERS, {})
                   .subscribe((r) => {
                   
                    const {id} = this.loginCookieStorage.get();

                    r.forEach(element => {

                      const coords = new google.maps.LatLng(Number(element.location.lat), Number(element.location.lng));
                      const i = this.overlays.findIndex(({id}) => id == element.id);

                      if(i > -1){

                        this.overlays[i].setPosition(coords);
                        
                      }else{
                        this.overlays.push(this.createMarcker(coords, element.name, id == element.id ? ME : VEHICLE_ICON, element.id));
                      }
                     });
                   });
  }

  private loadStyle(){
    this.style = {
      width: '100%',
      height: '420px',
      'margin-bottom': '1em',
      'margin-top': '1em',
    };

    if (this.mobile) {
      this.style.height = '380px';
    }
  }

 private async regionMyPosition(): Promise<void>{

    let location = this.location;

      if (navigator.geolocation) {
         
           navigator.geolocation.getCurrentPosition(function(position) {
                      
            location.lat = position.coords.latitude;
           location.lng = position.coords.longitude;
      },

      function error(msg){alert('Please enable your GPS position feature.');},

      {
        maximumAge:10000, 
        timeout:5000, 
        enableHighAccuracy: true});

  } else {
      alert("Geolocation API is not supported in your browser.");
  } 

  setTimeout(() => {
     
    this.options = {
      center: { lat: location.lat, lng: location.lng },
      zoom: 12,
    };

    this.location = location;

   }, 1000);
  
  }

  private async myPosition(): Promise<void> {

    const { name, id } = this.loginCookieStorage.get();
    let location = { lat: null, lng: null };
    let coords 

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(function (position) {

        location.lat = position.coords.latitude;
        location.lng = position.coords.longitude;
        coords = new google.maps.LatLng(Number(position.coords.latitude), Number(position.coords.longitude));
      },

        function error(msg) { alert('Please enable your GPS position feature.'); },

        {
          maximumAge: 10000,
          timeout: 5000,
          enableHighAccuracy: true
        });

    } else {
      alert("Geolocation API is not supported in your browser.");
    }

    setTimeout(() => {
      this.overlays[0] = this.createMarcker(coords, name,  ME, id);
    }, 1000);

  }

  private trakinback(): void{

    if(this.startPerfil.findAuthorizationPerfil(this.componentKey)){
      this.interval = interval(5000).subscribe(() => {
        this.getLocationMarkers();
      });   
    }else{
      this.interval = interval(5000).subscribe(() => {
        this.myPosition();
      });   
    }
  }

  private createMarcker(coords, name, icon, id): any{
    return new google.maps.Marker({
                                      position: coords,
                                      title: name,
                                      id: id,
                                      icon: icon,
                                      map: this.map,
                                      navigationControlOptions: {
                                        style: google.maps.NavigationControlStyle.SMALL
                                    },
                                    mapTypeId: google.maps.MapTypeId.ROADMAP
                                    })
  }

  handleMapClick(event) {
   /*  this.dialogVisible = true;
    this.selectedPosition = event.latLng; */
  }

  handleOverlayClick(event) {
     this.data = event.overlay.data;
     this.dialogVisible = true;
  }

  public showName(){

    this.showInf = !this.showInf;

    if(this.showInf){
      this.overlays.forEach((overlay) => {
        let infoWindow = new google.maps.InfoWindow()
        infoWindow.setContent('' + overlay.title + '');
        infoWindow.open(this.map, overlay);
        this.infoWindow.push(infoWindow);
      });
    }else{
      this.infoWindow.forEach((event) => {
        event.close();
      });
    }
  }

  addMarker() {}

  handleDragEnd(event) {}

  onMapReady(event){
   this.map = event.map;
  }

  initOverlays() {
    this.mks = !this.mks;

    if(!this.mks){

      for (let index = 0; index < this.overlays.length; index++) {
        const element = this.overlays[index];
        
        if(element?.tag){
          this.overlays.splice(index, 1);
        }
        
      }
       return;
    }

    this.apiService.get<any>(ApiPaths.GMAP, {}).subscribe((r) => {

      let marker = {
        position: null,
        title: null,
        tag: 0,
        icon: null,
        data: null,
            navigationControlOptions: {
              style: google.maps.NavigationControlStyle.SMALL
          },
          mapTypeId: google.maps.MapTypeId.ROADMAP
      }

      const list = [];

      r.supplier.forEach(element => {

        marker.title = 'Fornecedor: ' + element.fantasyName;
        marker.data = element;

        element.companyData.forEach((address) => {
          
          if(address.address.location){
            marker.icon = SUPLIER_ICON;
            marker.tag++
            marker.position = { lat:Number( address.address.location.lat), lng: Number(address.address.location.lng) };
            this.overlays.push( new google.maps.Marker({...marker}));
          }
        });
      });

      r.customers.forEach(element => {
       
        marker.title =  'Cliente: ' + element.fantasyName;
        marker.data = element;

        element.companyData.forEach((address) => {
          if(address.address.location){
           
            marker.icon = CUSTUMER_ICON;
            marker.tag++
            marker.position = { lat:Number( address.address.location.lat), lng: Number(address.address.location.lng) };
            this.overlays.push( new google.maps.Marker({...marker}));
          }
        });
      });

    });
  }

  zoomIn(map) {
    map.setZoom(map.getZoom() + 1);
  }

  zoomOut(map) {
    map.setZoom(map.getZoom() - 1);
  }

  clear() {
    this.overlays = [];
  }

  ngOnDestroy() {
    this.interval.unsubscribe();
  }
}


